            <div class="news-content__wrap">
                <div class="news-content__header">
                    <h2><router-link to="#">TIN TỨC SỰ KIỆN</router-link></h2>
                </div>
                <div class="news-content__title">
                    <h3>Xứng đáng là những chiến sỹ xung kích trên mặt trận tư tưởng, văn hoá của Đảng</h3>
                    <p>Th 2, 01/08/2022 | 08:03 SA</p>
                </div>
                <div class="news-content__details content-details">
                    <p class="content-details--title">Kỷ niệm 92 năm Ngày truyền thống ngành Tuyên giáo của Đảng trong bối cảnh toàn Đảng, toàn dân, toàn quân đang tích cực triển khai Nghị quyết Đại hội đi vào cuộc sống trên từng lĩnh vực trọng yếu cơ bản. Với niềm tin tưởng mãnh liệt, đội ngũ những người làm công tác tư tưởng, công tác tuyên giáo sẽ kiên định những quan điểm có tính nguyên tắc, không ngừng đổi mới nội dung và phương thức để đáp ứng tình hình mới trong giai đoạn cách mạng hiện nay.</p>
                    <p class="content-details--img">
                        <img src="https://file1.dangcongsan.vn/data/0/images/2022/07/31/trucbandang/ky-niem-92-na%CC%86m-ngay-tuyen-giao.jpg?dpi=150&quality=100&w=780" alt="">
                    </p>
                    <p>Năm nay toàn Đảng, toàn dân, toàn quân ta kỷ niệm 92 năm Ngày truyền thống ngành Tuyên giáo của Đảng trong bối cảnh đất nước đang có nhiều thành tựu đổi mới thắng lợi. Đồng thời cũng có nhiều thách thức đang đặt ra. Nghị quyết Đại hội lần thứ XIII của Đảng nêu rõ: “Tiếp tục đổi mới mạnh mẽ nội dung, phương thức công tác tư tưởng theo hướng chủ động, thiết thực, kịp thời và hiệu quả; nâng cao tính chiến đấu, tính giáo dục, tính thuyết phục trong tuyên truyền, học tập chủ nghĩa Mác - Lê-nin, tư tưởng Hồ Chí Minh, quan điểm, đường lối của Đảng, chính sách, pháp luật của Nhà nước. Nắm chắc, dự báo đúng đắn, kịp thời các vấn đề tư tưởng trong Đảng, trong xã hội. Chú trọng nắm bắt, định hướng dư luận xã hội. Đẩy mạnh tổng kết thực tiễn, nghiên cứu lý luận, hoàn thiện hệ thống lý luận về đường lối đổi mới về chủ nghĩa xã hội và con đường đi lên chủ nghĩa xã hội ở Việt Nam… Tăng cường bảo vệ nền tảng tư tưởng của Đảng, kiên quyết và thường xuyên đấu tranh phản bác các quan điểm sai trái, thù địch, cơ hội chính trị; đấu tranh, ngăn chặn, đẩy lùi sự suy thoái về tư tưởng chính trị, đạo đức, lối sống, những biểu hiện “tự diễn biến”, “tự chuyển hoá” trong nội bộ”… </p>
                    <p>Thời gian qua, vượt lên nhiều khó khăn, thách thức, nhất là ảnh hưởng của đại dịch COVID-19 trong suốt hai năm, ngành Tuyên giáo không ngừng đổi mới, từng bước nâng cao chất lượng và đạt được những thành tựu quan trọng, như: Tiếp tục đẩy mạnh tuyên truyền Nghị quyết Đại hội XIII của Đảng đi vào cuộc sống và 07 Hội nghị toàn quốc về những lĩnh vực quan trọng của Đại hội: Kinh tế, Xây dựng Đảng, Văn hóa, Nội chính, Mặt trận Tổ quốc, Đối ngoại… và các Hội nghị phát triển theo vùng của cả nước. Đáng chú ý, sau nhiều năm Bộ Chính trị tổ chức Hội nghị toàn quốc về văn hóa theo tinh thần “văn hóa phải đặt ngang hàng với kinh tế và chính trị”. Đặc biệt, ngành tuyên giáo đã tập trung hướng dẫn, triển khai đợt sinh hoạt chính trị, tư tưởng trong toàn Đảng về tác phẩm “Một số vấn đề lý luận và thực tiễn về Chủ nghĩa xã hội và con đường đi lên Chủ nghĩa xã hội ở Việt Nam” của Tổng Bí thư Nguyễn Phú Trọng. Tuyên truyền công tác xây dựng, chỉnh đốn Đảng gắn với triển khai Chỉ thị số 05-CT/TW của Bộ Chính trị “Về đẩy mạnh học tập và làm theo tư tưởng, đạo đức, phong cách Hồ Chí Minh”; lĩnh vực khoa học – công nghệ không ngừng được đẩy mạnh gắn với cuộc cách mạng công nghiệp lần thứ tư, trong đó chuyển đổi số được triển khai mạnh mẽ ở nhiều lĩnh vực.</p>
                    <p>Tuy nhiên, công tác tuyên giáo trong thời gian qua còn có những hạn chế, khó khăn, như: Việc triển khai học tập, quán triệt nghị quyết, chỉ thị của Đảng còn bất cập; công tác xây dựng Đảng về chính trị, tư tưởng, đạo đức tuy đã được cấp ủy các cấp quan tâm lãnh đạo, chỉ đạo song nội quy, quy trình, phương thức triển khai thực hiện chưa cụ thể, còn lúng túng nên hiệu quả chưa rõ nét; chất lượng đào tạo, bồi dưỡng lý luận chính trị ở các cơ sở đào tạo, bồi dưỡng chuyển biến chậm; công tác chỉ đạo, định hướng, quản lý báo chí ở một số đơn vị, địa phương vẫn còn bất cập, việc thực hiện tôn chỉ, mục đích của một số báo, tạp chí chưa nghiêm, vẫn còn tình trạng “báo hóa” tạp chí điện tử; việc thực hiện chủ trương, đường lối của Đảng về các lĩnh vực: khoa học – công nghệ, giáo dục – đào tạo, văn hóa, ý tế có mặt chưa đáp ứng được yêu cầu của thực tiễn…</p>
                    <p>Kỷ niệm 92 năm Ngày truyền thống ngành Tuyên giáo của Đảng trong bối cảnh toàn Đảng, toàn dân, toàn quân đang tích cực triển khai Nghị quyết Đại hội đi vào cuộc sống trên từng lĩnh vực trọng yếu cơ bản. Niềm tin vào sự lãnh đạo của Đảng và Nhà nước tăng lên trong các tầng lớp nhân dân. Đó là thời cơ để ngành Tuyên giáo thể hiện chức năng định hướng tư tưởng chính trị, tạo sự thống nhất cao trong Đảng và sự đồng thuận trong xã hội. Song, cũng cần thấy rằng công tác tuyên giáo bên cạnh những thuận lợi cũng đang đứng trước những khó khăn, thách thức lớn: Tình hình thế giới đang trải qua những biến động to lớn, diễn biến rất nhanh chóng, phức tạp, khó dự báo. Hoà bình, hợp tác và phát triển vẫn là xu thế lớn, song đang đứng trước nhiều trở ngại, khó khăn; cạnh tranh chiến lược giữa các nước lớn, xung đột cục bộ tiếp tục diễn ra dưới nhiều hình thức, phức tạp và quyết liệt hơn, làm gia tăng rủi ro đối với môi trường kinh tế, chính trị, an ninh quốc tế; chủ nghĩa dân tộc cực đoan, chủ nghĩa cường quyền nước lớn, chủ nghĩa thực dụng trong quan hệ quốc tế gia tăng. Cuộc xung đột quân sự Nga - Ucraina chưa có dấu hiệu kết thúc. NATO lợi dụng Nga tiến hành chiến dịch quân sự đặc biệt ở Ucraina đã tiến hành mở rộng. Khủng hoảng năng lượng và lương thực góp phần làm cho khủng hoảng kinh tế toàn cầu thêm gay gắt.</p>
                    <p>Ở trong nước, sau 35 đổi mới, thế và lực, sức mạnh tổng hợp quốc gia, uy tín quốc tế, niềm tin của nhân dân ngày càng được nâng cao, tạo ra những tiền đề quan trọng để thực hiện hai chiến lược xây dựng và bảo vệ Tổ quốc như Tổng Bí thư Nguyễn Phú Trọng đã khẳng định: “Đất nước ta chưa bao giờ có được cơ đồ, tiềm lực, vị thế và uy tín quốc tế như ngày nay”. Song, bên cạnh những thành tựu toàn diện đạt được rất quan trọng thì những khó khăn, hạn chế nội tại của nền kinh tế và thiên tai, biến đổi khí hậu, dịch bệnh, dịch bệnh, nhất là đại dịch COVID-19 ảnh hưởng lớn đến sự phát triển của nền kinh tế và các lĩnh vực khác của đời sống xã hội. Bốn nguy cơ mà Đảng ta đã chỉ ra còn tồn tại, có mặt còn gay gắt hơn, nếu nền kinh tế nước ta không phát triển nhanh hơn sẽ rơi vào “bẫy thu nhập trung bình” vẫn là thách thức lớn. Trong bối cảnh đó, phát huy truyền thống vẻ vang 92 năm qua, ngành Tuyên giáo nỗ lực vươn lên, vượt qua mọi thách thức, khó khăn, đáp ứng ngày càng cao của sự nghiệp đổi mới, xây dựng và bảo vệ Tổ quốc.</p>
                    <p class="content-details--end"><em>TS. Bùi Thế Đức, Phó Chủ tịch Hội đồng Lý luận, phê bình văn học, nghệ thuật TW/dangcongsan.vn</em></p>
