<template>
    <div id="slide">
        <div class="slide">
            <div class="slide--content" ref="Main_slide">
                <div class="slide--item" 
                v-for="(item_slick, index) in slick"
                :key="index" 
                ref="item_slide">
                    <router-link to="#">
                    <img :src="item_slick.img" :alt="item_slick.title">
                    <h2 class="slide--title">{{item_slick.title}}</h2>
                    <span class="slide--text">{{item_slick.content}}</span>
                    </router-link>
                </div>
            </div>
            <div class="slide--btn">
                <ol>
                    <li v-for="(btn,index) in slick" :key="index" :class="{'active': index==numberClick}" 
                    @click="autoslide(index),numberClick = index,deleteTime()">{{index + 1}}</li>
                </ol>
            </div>
        </div>
    </div>
</template>

<script>
import {ref} from 'vue';
export default {
    setup() {
    let itemSlide = ref('')
    let numberClick = ref(0)
    let autoclicks = ref(true)
    let slick = ref([
      {
        img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=161966&t=1658531969133",
        title:"Hội nghị toàn quốc nghiên cứu, học tập, quán triệt Nghị quyết Hội nghị lần thứ XX",
        content:"Sáng 21/07/2022, tại Hà Nội, Bộ Chính trị, Ban Bí thư Trung ương Đảng tổ chức Hội nghị toàn quốc nghiên cứu, học tập, quán triệt Nghị quyết Hội nghị lần thứ 5, Ban Chấp hành Trung ương Đảng khóa"
      },
      {
        img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=161831&t=1658532272061",
        title:"Công bố Quyết định của Ban Thường vụ Tỉnh ủy về công tác cán bộ",
        content:"Sáng 21/7/2022, Thường trực Tỉnh ủy tổ chức Hội nghị công bố Quyết định của Ban Thường vụ Tỉnh ủy về công tác cán bộ tại Liên đoàn Lao động tỉnh."
      },
      {
        img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=161801&t=1658532272081",
        title:"Đồng chí Trần Quang Tòa giữ chức Bí thư Huyện ủy Tư Nghĩa",
        content:"Chiều 20/7, Ủy viên Trung ương Đảng, Bí thư Tỉnh ủy, Chủ tịch HĐND tỉnh Bùi Thị Quỳnh Vân chủ trì Hội nghị công bố Quyết định của Ban Thường vụ Tỉnh ủy về công tác cán bộ."
      },
      {
        img:"https://www.quangngai.dcs.vn/image/journal/article?img_id=161801&t=1658532272081",
        title:"Đồng chí Trần Quang Tòa giữ chức Bí thư Huyện ủy Tư Nghĩa",
        content:"Chiều 20/7, Ủy viên Trung ương Đảng, Bí thư Tỉnh ủy, Chủ tịch HĐND tỉnh Bùi Thị Quỳnh Vân chủ trì Hội nghị công bố Quyết định của Ban Thường vụ Tỉnh ủy về công tác cán bộ."
      }
    ])
        return{
            slick,itemSlide,numberClick,autoclicks
        }
    },
  methods: {
    setSlide(){
      this.itemSlide = this.$refs.item_slide;
      for(let i = 0; i < this.itemSlide.length; i++){
        this.itemSlide[i].style = `left:${i*105}%`
      }
    },
    autoslide:function(n){
      this.$refs.Main_slide.style = `transform: translateX(-${n*105}%)`
    },
    loopSlide:function(){
      this.itemSlide[0].style = `left:${(this.itemSlide.length)*105}%`
      this.autoslide(this.itemSlide.length)
      this.numberClick = 0
      this.$refs.Main_slide.addEventListener('transitionend',this.resetSlide)
      
    },
    PlusSlide:function(){
      if(this.itemSlide.length>1){
        this.timeClick = setInterval(() => {
          if(!this.autoclicks)return
          this.numberClick++;
          if(this.numberClick > this.itemSlide.length-1){
            this.loopSlide()
            return
          }
          this.autoslide(this.numberClick)
        }, 6000)
      }
    },
    deleteTime(){
      this.autoclicks = false;
      this.settime = setTimeout(() =>{
        if(this.autoclicks == false){
          this.autoclicks = true;
        }
      },10000);
    },
    resetSlide(){
      this.setSlide();
      this.$refs.Main_slide.style = `transform: translateX(0);transition:none`;
      this.$refs.Main_slide.removeEventListener('transitionend',this.resetSlide);
    }
  },

  mounted() {
    this.setSlide();
    this.PlusSlide();
  },
  beforeUnmount() {
    clearInterval(this.timeClick)
    clearTimeout(this.settime)
  }
}
</script>

<style scoped lang="scss" src="@/assets/scss/components/_slide.scss"/>