<script>
export default {
  mixins: [],
  data() {
    return {
      btnShow:false,
      fixedMenu:false,
    }
  },
  methods: {
    headeralwaysTop(){
      let headerTop = this.$refs.headerTop;
      let headerBanner = this.$refs.headerBanner;
      let wdWidth = window.innerWidth;
      if(window.scrollY > headerBanner.clientHeight && wdWidth>767){
        headerTop.classList.add("always--top");
        headerBanner.style = `margin-bottom:${headerTop.clientHeight}px`
      }
      else{
        headerTop.classList.remove("always--top");
        headerBanner.style = `margin-bottom:0`
      }
    },
    showSearch(){
      this.$refs.formSearch.classList.toggle("shows_input")
      this.$refs.headerSp.classList.toggle("box-shadow-none")
      this.btnShow =! this.btnShow 
    },
    showMenu(){
      this.$refs.menuheaderSP.classList.toggle("show_menu")
      this.$refs.hamburgerMenu.classList.toggle("active_hamburger")
      this.$emit("fixedMenu", this.fixedMenu =! this.fixedMenu)
    },
    showInner(e){
      let element = e.target.nextSibling.nextSibling
      if(element){
        element.classList.toggle("showInner")
        element.parentElement.classList.toggle("active")
        e.target.classList.toggle("arrow_bottom")
      }
    }
  },
  created() {
    window.addEventListener('scroll', ()=>{
      this.headeralwaysTop()
    })
  },
}
</script>
